.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
  width: 100%;
  height: 10rem;
  padding: 4rem 0;
  margin-top: 0.2rem;
  transition: all 0.4s ease;
}
.header.shrinked {
  height: 5rem;
  padding: 1.7rem 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
@media screen and (max-width: 800px) {
  .header {
    padding: 1.7rem 0;
    height: 5rem;
  }
  .header[data-nav-active=true] {
    height: 8.5rem;
    padding: 1.7rem 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }
}
.header a {
  color: #222;
}
.content {
  margin: 0 auto;
  width: 80rem;
  min-width: 65%;
  max-width: 85%;
}
.content:after {
  content: '.';
  height: 0;
  visibility: hidden;
  clear: both;
}
.title {
  float: left;
  font-size: 1.8rem;
}
.nav {
  float: right;
  transition: all 0.5s ease;
  max-width: 80%;
}
.nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  height: 2rem;
  line-height: 2rem;
  overflow-y: hidden;
}
.nav ul li {
  margin-left: 1.6rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 800px) {
  .nav {
    display: none;
    max-width: initial;
  }
  .nav.collapse {
    display: initial;
    margin-top: 1.8rem;
  }
}
.hamburger {
  float: right;
  display: none;
}
@media screen and (max-width: 800px) {
  .hamburger {
    display: initial;
  }
}



