.twinkling > div {
  animation: twinkling 1.1s infinite ease-in-out;
  animation-direction: alternate;
}
@keyframes twinkling {
  from {
    background-color: #d5e4b2;
    color: #333;
  }
  to {
    background-color: #dfebc5;
    color: #666;
  }
}



