.button {
  display: none;
}
@media screen and (max-width: 800px) {
  .button {
    display: inline-block;
    width: 22px;
  }
}
.button .line {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #222;
  margin-bottom: 6.2px;
  transition: all 0.5s ease;
}
.button .line:last-child {
  margin-bottom: 0;
}
.button[data-active=true] .line:first-child {
  transform: translateY(7.161px) translateX(3%) rotate(45deg);
}
.button[data-active=true] .line:nth-child(2) {
  opacity: 0;
}
.button[data-active=true] .line:last-child {
  transform: translateY(-7.161px) translateX(3%) rotate(-45deg);
}



